import gql from 'graphql-tag'

import { Api } from '../api'
import { InvalidateFn } from '../selectors'

import { UserInput } from './createOrganizationWithUser'

interface ExistingUserInput extends UserInput {
  id: number
}

export const updateUserMutation = gql`
  mutation ($id: Int!, $user: IndividualInput!) {
    updateUser(id: $id, user: $user)
  }
`

export const updateUser =
  (api: Api, invalidate: InvalidateFn) =>
  async (user: ExistingUserInput, isSelf: boolean): Promise<boolean> => {
    try {
      const { id, ...restUser } = user
      const data = await api.mutation<{
        updateUser: number // NOTE: id of user
      }>(updateUserMutation, { id, user: restUser })

      const success = !!data.updateUser

      if (success) invalidate(isSelf ? 'myUser' : 'users')

      return success
    } catch (error) {
      return false
    }
  }
