import gql from 'graphql-tag'

import { Api } from '../api'
import { InvalidateFn } from '../selectors'

export const updateUserRolesMutation = gql`
  mutation ($id: Int!, $roleIds: [Int]!) {
    updateUserRoles(userId: $id, roleIds: $roleIds)
  }
`

export const updateUserRoles =
  (api: Api, invalidate: InvalidateFn) =>
  async ({ id, roleIds }: { id: number; roleIds: number[] }): Promise<boolean> => {
    try {
      const data = await api.mutation<{
        updateUserRoles: number // NOTE: id of user
      }>(updateUserRolesMutation, { id, roleIds })

      const success = !!data.updateUserRoles

      if (success) invalidate('users')

      return success
    } catch (error) {
      return false
    }
  }
