import gql from 'graphql-tag'

import { Api } from '../api'
import { InvalidateFn } from '../selectors'

export interface OrganizationInput {
  name: string // -> organizationName
  typeId: number // -> organizationTypeId
  address1: string
  address2: string
  city: string
  usStateId: number | null
  countryId: number
  zipCode: string
  phone: string
}

export const createOrganizationMutation = gql`
  mutation ($organization: OrganizationInput!) {
    createOrganization(organization: $organization)
  }
`

export const createOrganization =
  (api: Api, invalidate: InvalidateFn) =>
  async (organization: OrganizationInput): Promise<number | null> => {
    try {
      const { name: organizationName, typeId: organizationTypeId, ...restOrganization } = organization
      const data = await api.mutation<{
        createOrganization: number | null // NOTE: new organization ID
      }>(createOrganizationMutation, { organization: { organizationName, organizationTypeId, ...restOrganization } })

      const id = data.createOrganization

      if (id) invalidate('organizations')

      return id
    } catch (error) {
      return null
    }
  }
